import api from "../Api";

const generat = async function (data) {
  return await api.post("/generat", data);
};

const getPercentage = async function () {
  return await api.post("/get-percentage");
};

const getAllImage = async function () {
  return await api.post("/get-all-image");
};

const testGenerat = async function (data) {
  return await api.post("/test-generat", data);
};

const setProperties = async function (data) {
  return await api.post("/set-properties", data);
};

export default {
  generat,
  getPercentage,
  getAllImage,
  testGenerat,
  setProperties,
};
